/* ---------------------
  
          GENERAL
  
  ------------------------*/
*,
*::after,
*::before {
  box-sizing: border-box;
}
html {
  width: 100%;
  height: 100vh;
  line-height: 1;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}
html a {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
  text-decoration: none;
}
body {
  width: 100%;
  height: 100vh;
  font-family: 'Open Sans', sans-serif;
}
::-webkit-scrollbar {
  width: 5px;
  /* for vertical scrollbars */
  height: 5px;
  /* for horizontal scrollbars */
}
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar-thumb {
  background: #141822;
}

/* Style for header */
header {
  position: fixed;
  background-color: #141822;
  width: 100%;
  top: 0px;
  z-index: 5;
  box-shadow: 0px 1px 10px 0px #000;
}
.line-decoration {
  height: 64px;
  margin-top: -1px;
  background: linear-gradient(to top left, transparent 50%, #141822 51%);
  margin-bottom: 36px;
}
.line-decoration-inverse {
  height: 64px;
  margin-bottom: -1px;
  background: linear-gradient(to top right, #141822 50%, transparent 51%);
  margin-top: 36px;
}

/* ---------------------
  
  COLORS
  
  ------------------------*/
.text-white {
  color: white;
}
.cl-prime {
  color: #4c5edc;
}
.cl-bg-prime-dark {
  background-color: #141822;
}

/* ---------------------
  
  TYPOGRAPHY AND SECTIONS
  
  ------------------------*/
a:hover {
  cursor: pointer;
}
h1,
h2 {
  text-align: center;
  text-rendering: optimizeLegibility;
}
h3,
h4 {
  text-align: left;
  text-rendering: optimizeLegibility;
}
h1,
h2,
h3 {
  font-family: 'Architects Daughter', cursive;
  letter-spacing: 1px;
}
h1 {
  font-size: 6vw;
  margin: 0.5em;
}
h2 {
  font-size: 5vh;
  margin: 0.5em;
}
h3 {
  font-size: 4.5vmin;
  margin: 1em;
}
h4 {
  font-size: 2.5vmax;
  letter-spacing: -1px;
}
@media screen and (max-width: 780px) {
h2 {
    font-size: 4.5vh;
}
h3 {
    font-size: 5.5vmin;
}
}
.f-arhitects {
  font-family: 'Architects Daughter', cursive;
}
.f-open-s {
  font-family: 'Open Sans', sans-serif;
}
.b-b {
  font-weight: bold;
}
.line-through {
  text-decoration: line-through;
  color: white;
}
.shadow {
  box-shadow: 0px 1px 10px 0px #000;
}
.o-7 {
  opacity: 0.7;
}
.o-5 {
  opacity: 0.5;
}
.slide-i-o:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  transition: width 0s ease, background .5s ease;
}
.slide-i-o:after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  background: #4c5edc;
  transition: width .5s ease;
}
.slide-i-o:hover:before {
  width: 100%;
  background: #4c5edc;
  transition: width .5s ease;
}
.slide-i-o:hover:after {
  width: 100%;
  background: transparent;
  transition: all 0s ease;
}
.slide-i-o {
  display: inline-block;
  position: relative;
  padding-bottom: 1px;
}
.slide-i-o:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  transition: width 0s ease, background .5s ease;
}
.slide-i-o:after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  background: #4c5edc;
  transition: width .5s ease;
}
.slide-i-o:hover:before {
  width: 100%;
  background: #4c5edc;
  transition: width .5s ease;
}
.slide-i-o:hover:after {
  width: 100%;
  background: transparent;
  transition: all 0s ease;
}
section {
  padding: 2em;
}
hr {
  border: 0;
  height: 1px;
  background-image: -o-linear-gradient(left, #f0f0f0, #8c8b8b, #f0f0f0);
}
p {
  margin: 2em 2em;
  line-height: 1.2;
}
@media screen and (max-width: 780px) {
p {
    margin: 1em 1em;
}
}
@media screen and (min-width: 780px) {
p {
    line-height: 1.2;
}
}
@media screen and (min-width: 1200px) {
p {
    line-height: 1.4;
}
}
.text-center {
  text-align: center;
}
.center-cf {
  position: absolute;
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  transform: translateX(-50%) translateY(-50%);
}
.center-tf {
  position: absolute;
  left: 50%;
  top: 25%;
  bottom: auto;
  right: auto;
  transform: translateX(-50%) translateY(-25%);
}
strong {
  color: #93969c;
}
@media screen and (max-width: 600px) {
section {
    padding: 1.5em;
}
}

/* ---------------------
  
          NAVIGATION
  
  ------------------------*/
header ul.nav {
  overflow: hidden;
  list-style: none;
}
header ul.nav li {
  position: relative;
  float: left;
  width: 25%;
}
header ul.nav li a {
  display: block;
  height: 50px;
  opacity: 0;
  line-height: 50px;
  color: #848e92;
  text-decoration: none;
  text-align: center;
  cursor: default;
  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
}
header ul.nav li a:hover {
  color: #fff;
  background-color: #1D2933;
  box-shadow: 0px 1px 10px 0px #000;
}
@media screen and (max-width: 600px) {
header ul.nav li {
    float: none;
    width: 100%;
}
header ul.nav li a {
    height: 0;
}
}

/* Style for header with active class name */
header.active ul.nav li a {
  height: 120px;
  opacity: 1;
  line-height: 120px;
  cursor: pointer;
}
@media screen and (max-width: 600px) {
header.active ul.nav li a {
    height: 60px;
    line-height: 60px;
    border-bottom: 1px solid #222b2f;
}
}

/* Style for the plus button */
button.toggle-nav {
  position: absolute;
  top: 50px;
  left: calc(50% - 30px);
  width: 60px;
  height: 35px;
  background-color: #4c5edc;
  font: 400 1.2em 'Allan', serif;
  color: #fff;
  border: none;
  line-height: 30px;
  vertical-align: middle;
  outline: none;
  cursor: pointer;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
  box-shadow: 0px 1px 10px 0px #000;
}
button.toggle-nav:hover {
  height: 50px;
}
button.toggle-nav span {
  display: block;
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
}

/* Style for the plus button when header has active classe name */
header.active button.toggle-nav {
  top: 120px;
  background-color: #4c5edc;
}
header.active button.toggle-nav span {
  -webkit-transform: rotate(270deg);
  -moz-transform: rotate(270deg);
}
@media screen and (max-width: 600px) {
button.toggle-nav {
    top: 0;
    left: 15px;
}
header.active button.toggle-nav {
    top: 240px;
}
}
span.underline-r {
  border-bottom: 2px solid #4c5edc;
}
.btn-link-to-subsection {
  width: 100%;
  padding: 3rem 2rem;
  text-align: center;
  /*position: absolute;*/
  bottom: 0;
  left: 0;
}
.btn-link-to-subsection:hover {
  color: white;
  background-color: #e61919;
}
.btn-link-to-subsection:hover a {
  width: 100%;
  height: 100%;
  color: white;
  padding: 3rem 2rem;
}

/* ---------------------
  
          Animations
  
  ------------------------*/
.component-fade-enter-active,
.component-fade-leave-active {
  transition: opacity .3s ease;
}
.component-fade-enter,
.component-fade-leave-to {
  opacity: 0;
}

/* ---------------------
  
          Main style
  
  ------------------------*/



