/* --------------------------------------------- 
 				

 				Responsive grid


 --------------------------------------------- */

/* ------ Clearfix metoda ------ */
.row:before, .row:after, .column:before, .column:after, .cf:before,
.cf:after , .divide:before, .divide:after{
    content: " ";
    display: table;
}
.row:after, .column:after, .cf:after, .divide:after {
    clear: both;
}

/* ------ IE6/7 fix ------ */
.row, .column, .cf, .divide{
    *zoom: 1;
}
.center{
	display: block;
	margin: 0 auto;
}

/* ------ Širina kolumne ------ */
.column-1 { width: 6.25%;
}
.column-2 { width: 14.58333333333333%;
}
.column-3 { width: 22.91666666666667%;
}
.column-4 { width: 31.25%;
}
.column-5 { width: 39.58333333333333%;
}
.column-6 { width: 47.91666666666667%;
}
.column-7 { width: 56.25%;
}
.column-8 { width: 64.58333333333333%;
}
.column-9 { width: 72.91666666666667%;
}
.column-10 { width: 81.25%;
}
.column-11 { width: 89.58333333333333%;
}
.column-12 { width: 97.91666666666667%;
}


/* ------ Divide klase ------ */
.divide-25 { width: 24.999999%;
}
.divide-50 { width: 49.999999%;
}
.divide-75 { width: 74.999999%;
}
.divide-100 { width: 99.999999%;
}


/* ------ Temeljne klase podjela ------ */
.divide {
	padding: 0 1.041666666666667%;
  	float: left;
}
.column {
	margin: 0 1.041666666666667%;
  	float: left;
}

/* ------ Offset ------ */
.offset-05 { margin-left: 4.1666666666%;
}
.offset-1 { margin-left: 8.33333333333%;
}
.offset-2 { margin-left: 16.66666666666%;
}
.offset-3 { margin-left: 25%;
}
.offset-4 { margin-left: 33.33333333333%;
}
.offset-5 { margin-left: 41.66666666666%;
}
.offset-6 { margin-left: 50%;
}
.offset-7 { margin-left: 58.33333333333%;
}
.offset-8 { margin-left: 66.66666666666%;
}
.offset-9 { margin-left: 75%;
}
.offset-10 { margin-left: 83.3333333333%;
}

/* ------ Redak ------ */
.row {
	max-width: 100%;
	margin: 0 auto;
}
.row-25 {
	max-width: 25%;
	margin: 0 auto;
}
.row-50 {
	max-width: 50%;
	margin: 0 auto;
}
.row-75 {
	max-width: 75%;
	margin: 0 auto;
}
.row-90 {
	max-width: 90%;
	margin: 0 auto;
}
.column-centered{
    float: none;
    margin: 0 auto;
}
.column-centered-m{
    float: none;
    margin: 2em auto;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
* html .clearfix             { zoom: 1;
} /* IE6 */
*:first-child+html .clearfix { zoom: 1;
} /* IE7 */
@media only screen and (max-width:780px) {
.column { 
		width: 100%; 
		margin: 0; 
		padding: 0 10px; 
		box-sizing: border-box;
}
.column-centered-m{
		margin: 2em auto;
}
.divide{
		width: 100%; 
		margin: 0; 
		padding: 0 10px; 
		box-sizing: border-box;
}
.row-25, .row-50, .row-75, .row-90, .row{
		max-width: 90%;
		overflow: auto;
}
}




